@import "ScssHelpers/_helpers_all.scss";

.contact_section_v2 {
	overflow: hidden;

	@include bp($point_3) {
		padding-bottom: 7.4rem;
	}
}

.contact_section_v2__bg {
	overflow: hidden;
	position: absolute;
	top: 0;
	left: -.4rem;

	height: 100%;

	background-image: $gradient;

	@include bp(415px, min) {
		width: clamp(calc(100% + .5rem), 120.4rem, 120.4rem);

		&.contact_section_v2__bg--mob:not(.contact_section_v2__bg--desktop) {
			display: none;
		}
	}

	@include bp(414px) {
		//width: clamp(41.4rem, 100%, 100vw);
		width: 100vw;
		left: 0;

		&.contact_section_v2__bg--desktop {
			display: none;
		}
	}
}

.contact_section_v2__bg_svg{
	width: 100%;
	height: 100%;

	will-change: opacity;

	&.contact_section_v2__bg_svg--v2_mod {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;
	}
}

.contact_section_v2__bg--mob img{
	@extend %cover_img;
}

.contact_section_v2__bg_img {
	@extend %cover_img;
	position: absolute;
	left: 0;
	top: 0;
}

.partnerBg {
	//width: 120.5rem;
	//height: 58.6rem;
	height: 100%;
	will-change: filter;
}

.contact_section_v2__in {
	@extend %global_flex_block_row_wrap_flex-start;

	position: relative;

	overflow: hidden;

	@include bp($point_2, min) {
		margin-right: -13.6rem;
		padding: 15.2rem 2.4rem 15.1rem;
	}

	@include bp($point_3) {
		margin: 0 calc(-1 * var(--gl-indent));
		padding: 8rem 2.4rem 10.4rem;
	}
}

.contact_section_v2__content {
	position: relative;

	margin: 0 auto;
	text-align: center;
	
	@include bp($point_2, min) {
		z-index: 101; // fix header overlay
	}
}

.contact_section_v2__title {
	font-weight: 800;
	letter-spacing: 0.006em;
	text-align: center;
	text-transform: uppercase;
	line-height: 1.3;
	color: $white;

	@include bp($point_2, min) {
		max-width: 54%;
		margin: 0 auto 7.5rem;

		font-size: 3.8rem;
	}

	@include bp($point_3) {
		margin: 0 auto 4.8rem;

		font-size: 2.4rem;
	}

	@include bp($point_3, $point_7) {
		max-width: 77%;
	}

	//.body--safari_state & {
	//	line-height: 1.55;
	//}
}
