@import "ScssHelpers/_helpers_all.scss";

.article_section {
	padding-bottom: 4rem;

	@include bp($point_2, min) {
		padding-top: 5.4rem;
	}

	@include bp($point_3) {
		padding: 3.6rem 6rem 7rem 7.3rem;
	}
}

.article_section_title {
	@extend %h1;
	will-change: filter, opacity;

	@include bp(1440px) {
		max-width: 88rem;
	}

	@include bp($point_2, min) {
		line-height: 1.07;

		&:not(:last-child) {
			margin-bottom: 7.8rem;
		}

		//.body--safari_state & {
		//	line-height: 1.45;
		//}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 6.4rem;
			font-size: 2.8rem;
			line-height: 1.26;
		}
	}
}

.article_section__text_in {
	position: relative;

	@include bp(1440px, $point_6 - 1) {
		max-width: 96rem;
	}

	@include bp($point_2, min) {
		padding-left: 3.8rem;
	}

	@include bp($point_6 - 1) {
		max-width: 27rem;
	}
}

.article_section__text {
	@extend %text;

	strong {
		font-weight: 800;
		text-indent: 3rem;
	}

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 3.6rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			text-indent: 2.6rem;
			margin-bottom: 2.8rem;
		}

		strong {
			line-height: 1.2;
		}
	}
}

.article_section_decor {
	position: absolute;
	top: 0;
	left: 0;

	margin-top: 3px;

	background-color: $blue;

	@include bp($point_2, min) {
		width: 0.7rem;
		height: 2.7rem;
		margin-left: 2px;
	}

	@include bp($point_3) {
		width: 0.5rem;
		height: 1.8rem;
	}
}
